body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

  #me {
    margin-left: 30%;
    margin-top: 5%;
  }

  #greet {
    margin-left: 30%;
  }

  #hmm {
    margin-left: 30%;
    margin-top: 5%;
  }

  #emm {
    margin-left: 60%;
    margin-top: -4.5%;
  }

  #bee {
    margin-left: 30%;
    margin-top: 2%;
  }

  #kiki {
    margin-left: 30%;
    margin-top: 3%;
  }

  #kaka {
    margin-left: 30%;
    margin-top: 2%;
  }

  #evree {
    margin-left: 33%;
    margin-top: -3.4%;
  }

  #bae {
    margin-left: 30%;
    margin-top: 5%;
  }

  button {
    background-color: #3483eb;
    color: white;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 31%;
    padding-right: 31%;
    border: #b3cbeb;
    border-radius: 10px;
  }

  :hoverbutton {
    background-color: #1b4174;
  }

  #hmm,#emm,#bee {
    font-size: 14px;
    position: relative;
    border: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #f2f2f2;
  padding: 12px;
  border-radius: 3px;
  width: 250px;
  font-size: 14px;
  }

  textarea {
    font-size: 14px;
    position: relative;
    border: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #f2f2f2;
  padding: 12px;
  border-radius: 3px;
  width: 250px;
  font-size: 14px;
  }

  @media only screen and (max-width: 900px) {
    #emm{
        margin-left: 30%;
        margin-top: 1.5%;
    }
  }

  @media only screen and (max-width: 900px) {
    #evree{
        margin-left: 30%;
        margin-top: 1.5%;
    }
  }