body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  
  header {
    margin-right: 30%;
    margin-left: 50%;
    margin-top: 5%;
  }
  
  img {
    border-radius: 140px;
    height: 50px;
    width: 50px;
    margin-top: -190px;
  }
  
  @media only screen and (max-width: 600px) {
    img {
      margin-top: 20px;
    }
  }
  
  #place {
    margin-right: 30%;
    margin-left: 48%;
    margin-bottom: 3%;
  }
  
  @media only screen and (max-width: 600px) {
    #place {
      margin-right: -70%;
      margin-left: 45%;
    }
  }
  
  ul {
    list-style-type: none;
  }
  
  #twitter {
    border: 1px solid #eaecf0;
    display: flex;
    margin-left: 20%;
    margin-right: 20%;
    justify-content: center;
    padding-bottom: 20px;
    padding-top: 20px;
    margin-bottom: 14px;
    text-decoration: none;
    color: black;
    font-weight: 500;
    border-radius: 8px;
    box-shadow: inset 0 0 0 100px #eaecf0;
  }
  
  :hover#twitter {
    border-radius: 14px;
    box-shadow: inset 0 0 0 100px grey;
    transition-duration: 0.9s;
  }
  
  #slack {
    display: none;
  }
  
